import segmentedControl from '../components/tabbar-own/tabbar-own.vue';
import neilModal from '../../components/neil-modal/neil-modal.vue';
import uniTag from '../../components/uni-tag/uni-tag.vue';
import uniSection from '../../components/uni-section/uni-section.vue';
import uniNoticebar from '../../components/uni-notice-bar/uni-notice-bar.vue';
import timeSelector from '../../components/wing-time-selector/wing-time-selector.vue';
import { request } from '../../utils/request.js';
import mediaItem from './news-item.nvue';
export default {
	components: {
		segmentedControl,
		neilModal,
		uniTag,
		uniSection,
		uniNoticebar,
		timeSelector,
		mediaItem
	},
	data() {
		return {
			// tab选项卡信息
			items: ['主播列表', '主播数据'],
			// 判断展示主播列表还是数据
			current: 0,
			// 添加主播模态框是否显示
			show4: false,
			// 主播列表信息
			zblist: [],
			// 是否有主播列表
			haszblist: 0,
			// 主播收入数据信息
			zbdata: [],
			// 是否有主播收益数据
			haszbdata: 0,
			// 排行榜信息
			ranklist: [],
			// 时间选择器的当前时间
			time: new Date().toLocaleDateString(),
			// 查询收益的起止时间
			endDate: '',
			// 登录的token
			token: '',
			// 添加的主播信息
			anchorInfo: {
				// 添加的账号
				third_user_id: '',
				// 真实姓名
				real_name: '',
				// 手机号
				mobile: ''
			},
			// noticebar文字信息
			rank_string: '',
			// 查询时间
			date: '',
			// 查询页码
			page: '',
			// 直播平台
			tabBars: []
		};
	},
	mounted() {
		// 挂载成功后获取平台等信息
		this.getPlat();
		this.setEnddate();
		this.getToken();
		this.getZblist();
		this.getRanklist();
		this.setNoticebar();
		this.getZbdata();
	},
	onLoad(option) {
		// 用户通过分享的二维码扫码进入的用户关系绑定
		if (option.scene) {
			let qrId = decodeURIComponent(option.scene);
			request({
				url: '/api/user/creatRelation',
				data: { pid: qrId }
			}).then(res => {
				if (res.data.code == 1) {
					uni.showToast({
						title: '关系绑定成功',
						icon: 'success'
					});
				}
			});
		}
		// 通过分享小程序点击小程序卡片进入
		if (uni.getStorageSync('ppid')) {
			let pid = Number(uni.getStorageSync('ppid'));
			request({
				url: '/api/user/creatRelation',
				data: { pid: pid }
			}).then(res => {
				if (res.data.code == 1) {
					uni.showToast({
						title: '关系绑定成功',
						icon: 'success'
					});
				}
			});
		}
	},
	// 设置用户转发分享信息
	onShareAppMessage(res) {
		let pid = JSON.parse(uni.getStorageSync('login')).user_id;
		return {
			title: '天天赚钱',
			path: `/pages/anchor/anchor?pid=${pid}`,
			imgUrl: 'https://ww1.yunjiexi.club/2020/03/18/GwFBk.png'
		};
	},
	methods: {
		// 获取直播平台列表
		getPlat() {
			request({
				url: '/api/platform/lists'
			}).then(res => {
				this.tabBars = res.data.data;
			});
		},
		// 设置时间选择器截止时间
		setEnddate() {
			let endtimeYear = new Date().getFullYear();
			let endtimeMonth = new Date().getMonth() + 1;
			let endtimeDay = new Date().getDate();
			this.endDate = `${endtimeYear}-${endtimeMonth}-${endtimeDay}`;
		},
		// 获取并保存token
		getToken() {
			this.token = uni.getStorageSync('token');
		},
		// 设置底部提现人员列表信息
		setNoticebar() {
			this.$refs.noticebar.text = this.rank_string;
		},
		// 获取主播列表信息
		getZblist() {
			const that = this;
			request({
				url: '/api/anchor/lists',
				method: 'GET'
			}).then(res => {
				if (res.data.data.list) {
					that.zblist = res.data.data.list;
					that.rank_string = res.data.data.rank_string;
					that.haszblist = 1;
				} else {
					that.haszblist = 0;
				}
			});
			// 获取的返回值如果有 就把haszblist赋值为1，否则赋值为0
		},
		// 获取主播收益数据
		getZbdata() {
			const that = this;
			request({
				url: '/api/anchor/AnchorData',
				data: { date: this.date, page: this.page }
			}).then(res => {
				if (res.data.data.length != 0) {
					that.haszbdata = 1;
				} else {
					that.haszbdata = 0;
				}
				that.zbdata = res.data.data;
			});
		},
		// 获取昨日排行榜数据
		getRanklist() {
			const that = this;
			request({
				url: '/api/Rank/yesterList'
			}).then(res => {
				that.ranklist = res.data.data;
			});
		},
		// 点击跳转到排行榜列表页
		navagateto() {
			uni.navigateTo({
				url: '`./ranklist/ranklist?`+encodeURIComponent(JSON.stringify(this.ranklist))'
			});
		},
		/**
		 * 点击segmentedControl 事件回调
		 */
		onClickItem(index) {
			if (this.current !== index) {
				this.current = index;
			}
		},
		// 点击添加新主播，调出模态框
		addzb() {
			this.iShow = true;
		},
		// 关于模态框的函数
		bindClick() {
			this.show4 = true;
		},
		closeModal() {
			this.show4 = false;
		},
		bindBtn(type) {
			this.show4 = false;
		},
		// 提交增加主播信息
		addzbinfo() {
			// 发请求提交主播信息
			console.log('提交');
			// 先做表单校验，再发请求提交
			let regMob = /^[1][3,4,5,,6,7,8,,9,4][0-9]{9}$/;
			if (!this.anchorInfo.third_user_id) {
				uni.showToast({
					title: '抖音账号不能为空',
					icon: 'none'
				});
				this.show4 = true;
				return;
			}
			if (!this.anchorInfo.real_name) {
				uni.showToast({
					title: '真实姓名不能为空',
					icon: 'none'
				});
				this.show4 = true;
				return;
			}
			console.log(regMob.test(this.anchorInfo.mobile));
			if (!regMob.test(this.anchorInfo.mobile)) {
				uni.showToast({
					title: '请输入正确格式手机号',
					icon: 'none'
				});
				this.show4 = true;
				return;
			}
			const that = this;
			request({
				url: '/api/anchor/create',
				method: 'POST',
				data: this.anchorInfo
			}).then(res => {
				if (res.data.code == 1) {
					that.anchorInfo = {};
					uni.showToast({
						title: '添加成功'
					});
					that.getZblist();
				} else {
					uni.showToast({
						title: res.data.msg,
						icon: 'none'
					});
				}
			});
			// 提交后重新获取主播列表数据
			this.getZblist()
		},
		// 时间选择器相关函数
		btnConfirm(e) {
			// 确定选择时间后发请求请求对应时间的主播数据
			this.time = e.key;
			this.date = e.key;
			this.getZbdata();
		}
	}
};
